<template>
    <div class="mBg">
            <mhead-top titleHead="选择发票" lfIcon="el-icon-arrow-left icon" lfUrl="back" rgIcon="el-icon-shopping-cart-1 icon" rgUrl="/cart/cart"></mhead-top>
            <div class="addBorder"></div>
            <div class="m-o-address bbGray" v-for="(item,index) in list" :key="index">
                <div class="m-o-check">
                    <el-checkbox v-model="item.default" name="invoiceBox" @change="checkBtn(item.invoice_id)"></el-checkbox>
                </div>
                <div class="mo-a-name">
                    <div class="wto">{{item.type==0?'个人':'单位'}}</div>
                    <span :class="item.default?'':'gray'">默认</span>
                </div>
                <div class="mo-a-info">
                    <div class="mo-a-phone">{{item.title}}</div>
                </div>
                <div class="mo-a-arrow">
                    <router-link to="/userCenter/invoice/add">
                        <i class="el-icon-edit-outline icon"></i>
                    </router-link>
                </div>
            </div>

            <router-link to="/userCenter/invoice/add" class="m-p-foot">
                <el-button type="primary" class="m-p-Btn"><i class="el-icon-plus el-icon--left"></i>添加发票</el-button>
            </router-link>
        </div>
</template>


<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import mheadTop from '../../components/mobile/headTop';
    import {invoice} from '../../service/api'

    export default {
        name: 'mOrderInvoice',
        data(){
            return{
                list:'',
                choosedInvoice:''
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.getUserInfo();
            this.initData();

        },
        computed:{
            ...mapState([
                'userInfo',
            ]),


        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            async initData(){
                console.log("herht");
                if (this.userInfo && this.userInfo.user_id){
                    console.log("init");
                    let res = await invoice();
                    this.list = [...res];
                }
            },
            checkBtn(invoiceId){
                console.log(invoiceId);
                this.choosedInvoice = invoiceId;
                this.$router.push({ name: 'mOrderConfirm', params: { invoiceId: invoiceId }});
            }

        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
      /*min-height: px2rem(1140);*/
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .addBorder{
        display: block;
        width: 100%;
        height: .13333rem;
        background: url(../../images/addBorder.png) repeat-x;
        background-size: .8rem .13333rem;
    }
    .m-o-address{
        background-color: #fff;
        padding:px2rem(20);
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .m-o-check{
        width:px2rem(70);
        text-align: left;
        line-height: px2rem(100);
    }
    .mo-a-name{
        width:px2rem(130);
    }
    .mo-a-name .green{
        border:1px solid $default;
        border-radius: 3px;
        font-size: px2rem(22);
        color:$default;
        padding:0 px2rem(10);
    }
    .mo-a-name .gray{
        border:1px solid $bc;
        border-radius: 3px;
        font-size: px2rem(22);
        color:#999;
        padding:0 px2rem(10);
    }
    .mo-a-info{
        width:px2rem(460);
    }
    .mo-a-address{
        font-size: px2rem(26);
        color:#999;
        margin-top: px2rem(4);
    }
    .mo-a-arrow{
        width:px2rem(50);
        text-align: right;
        line-height: px2rem(100);
    }
    .mo-a-arrow .icon{
        font-size: px2rem(36);
        color:#999;
    }

    .m-p-foot{
        position: fixed;
        bottom:0;
        left:0;
        width:100%;
        z-index: 1000;
    }
    .m-p-Btn{
        border-radius: 0;
        width:100%;
        height: px2rem(104);
    }


</style>



